import React from "react";
import {inject} from "mobx-react/index";

@inject('goodStore')
class Header extends React.Component {

	/**
	 *    .
	 */
	addGood() {
		this.props.goodStore.create();
	}

	/**
	 *    .
	 */
	loadAll() {
		this.props.goodStore.loadAll();
	}

	/**
	 *    .
	 */
	render() {
		return (
			<div>
				<button onClick={() =>
					this.loadAll()}>   </button>

				<button onClick={() =>
					this.addGood()}> </button>
			</div>
		)
	};
}

export default Header;


